.bold-text {
  font-weight: bold;
  color: #000; /* Black color */
}

.app-container {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.app-title {
  color: #333;
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
}

.post-details {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.loading-gif-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 500px; /* Adjust the height as needed */
}

.loading-gif {
  width: 300px; /* Adjust the width as needed */
}

.post-title {
  color: #007bff;
  font-size: 20px;
  margin-bottom: 10px;
}

.post-body {
  color: #555;
  line-height: 1.6;
}

.post-info {
  font-style: italic;
  color: #888;
  margin-top: 10px;
}

.loading-message {
  text-align: center;
  font-size: 16px;
  color: #555;
  margin-top: 20px;
}
