/* APITwo.css */

.api-two-container {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .api-two-title {
    color: #333;
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .post-list {
    list-style: none;
    padding: 0;
  }
  
  .post-list li {
    background-color: #f9f9f9;
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  
  .bold-text {
    font-weight: bold;
    color: #000; /* Black color */
  }
  
  .loading-message {
    text-align: center;
    font-size: 16px;
    color: #555;
    margin-top: 20px;
  }

  .loading-gif-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 500px; /* Adjust the height as needed */
  }
  
  .loading-gif {
    width: 300px; /* Adjust the width as needed */
  }
  